export default{
	data(){
		return{
			recommendSongList:[]
		}
	},
	onLoad(){
		uni.request({
			url:this.$baseURL+'/playlist',
			success: (res) => {
				this.recommendSongList=res.data
				console.log("----获取推荐歌单数据----->",res)
			}
			
		})
	},
	methods:{
		//去推荐歌单详细页面
		torRecommendDetail(data){
			console.log("----》",data)
			uni.redirectTo({
				url:'../songListDetail/songListDetail?upPage=1&data='+encodeURIComponent(JSON.stringify(data))
			})
		}
	}
}
